<template>
  <div style="padding: 10px;">
<!--    功能区-->
    <div>
      <el-button type="primary" @click="add">新增</el-button>
      <el-button type="primary">导入</el-button>
      <el-button type="primary" @click="Export">导出</el-button>
    </div>

<!--    搜索区-->
    <div style="padding-top: 10px">
      <el-input v-model="search" placeholder="请输入账号关键字查找" style="width: 40%" clearable/>
      <el-button type="primary" style="margin-left: 5px" @click="load">搜索</el-button>
    </div>

    <el-table :data="tableData" stripe style="width: 100%;">
      <el-table-column prop="userName" label="账号" />
      <el-table-column prop="userPassword" label="密码" />
      <el-table-column prop="createTime" label="创建时间" sortable=""/>
      <el-table-column prop="role" label="权限" />
      <el-table-column label="操作" align='center'>
        <template #default="scope">
          <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
          <el-popconfirm title="确认删除吗?" @confirm="handleDelete(scope.row.userId)">
            <template #reference>
              <el-button type="danger" size="mini">删除</el-button>
            </template>
          </el-popconfirm>
          <el-popconfirm title="确认重置密码吗?" @confirm="handleUpdate(scope.row.userName)">
            <template #reference>
              <el-button type="danger" size="mini">重置</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <div style="margin: 10px 0">
      <el-pagination
          v-model:currentPage="currentPage"
          :page-sizes="[5, 10, 20, 25]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
      >
      </el-pagination>

      <el-dialog :title= "titleType" v-model="dialogVisible" width="30%">
        <el-form :model="form" label-width="120px" :rules="rules">
          <el-form-item label="账号" prop="userName">
            <el-input v-model="form.userName" style="width: 80%"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="userPassword">
            <el-input type="password" v-model="form.userPassword" style="width: 80%" show-password></el-input>
          </el-form-item>
          <el-form-item label="权限" prop="role">
            <el-select v-model="form.role" placeholder="请选择" style="width: 80%">
              <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <template #footer>
    <span class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="save">确 定</el-button>
    </span>
        </template>
      </el-dialog>
    </div>
  </div>
</template>

<script>


import request from "../utils/request";

export default {
  name: 'Home',
  components: {

  },
  data() {
    return {
      titleType: '',
      options: [{
        value: '管理员',
        label: '管理员'
      }, {
        value: '用户',
        label: '用户'
      }],
      form: {

      },
      dialogVisible: false,
      search: '',
      currentPage: 1,
      pageSize: 5,
      total: 0,
      tableData: [

      ],
      rules: {
        userName: [
          {required: true, message: '请输入用户名', trigger: 'blur'}
        ],
        userPassword: [
          {required: true, message: '请输入密码', trigger: 'blur'}
        ],
        role: [
          {required: true, message: '请输入用户权限', trigger: 'blur'},
        ],
      },
    }
  },
  //页面加载调用方法
  created() {
    this.load()
  },
  //事件
  methods: {
    load() {
      request.get("/api/user/findPage",{
        params: {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          search: this.search
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
    },
    add() {
      this.titleType = "添加";
      this.dialogVisible = true
      this.form = {}
    },
    save() {
      if(this.form.userId){
        this.$refs['form'].validate((validate) => {
          if (validate) {
              request.put("/api/user/update", this.form).then(res => {
                if (res.code === 20001) {
                    this.$message({
                    message: res.message,
                    type: 'error'
                  })
              }else{
                  this.$message({
                  message: res.message,
                  type: 'success'
                })
                this.dialogVisible = false;
                this.load();
              }
            })
          }
        })
      }else{
        this.$refs['form'].validate((validate) => {
          if (validate) {
            request.post("/api/user/save", this.form).then(res => {
              if (res.code === 20001) {
                this.$message({
                  message: res.message,
                  type: 'error'
                })
              } else {
                this.$message({
                  message: res.message,
                  type: 'success'
                })
                this.dialogVisible = false;
                this.load();
              }
            })
          }
        })
      }
    },
    handleEdit(row) {
      this.titleType = "修改";
      //深拷贝
      this.form = JSON.parse(JSON.stringify(row))
      this.dialogVisible = true;
    },
    handleDelete(userId) {
      request.delete("/api/user/delete/"+userId).then(res =>{
        if(res.code === 20001){
          this.$message({
            message: res.message,
            type: 'error'
          })
          this.form = {}
        }else {
          this.$message({
            message: res.message,
            type: 'success'
          })
        }
        this.load();
      })
    },
    handleUpdate(userName) {
      console.log(userName);
      request.put("/api/user/updateByName", userName).then(res =>{
        if(res.code === 20001){
          this.$message({
            message: res.message,
            type: 'error'
          })
        }else{
          this.$message({
            message: res.message,
            type: 'success'
          })
        }
        this.load();
      })
    },
    handleSizeChange(pageSize) { //改变分页当前的页数触发
      this.pageSize = pageSize;
      this.load();
    },
    handleCurrentChange(pageNum) { // 改变当前页码触发
      this.currentPage = pageNum;
      this.load();
    },
    Export(){

    },
  }
}
</script>
